.App {
  text-align: center;
  background-color: #f5f5f5;
  height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.timeCalc {
  margin: 10px 10px 0 10px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.timeCalc__paper {
  width: 100%;
  max-width: 600px;
  padding: 20px;
}

